"use client";

import { useParams, useRouter } from "next/navigation";
import { useState } from "react";
import { createApplication } from "@/utils/api"; // You already have this!
import { useAuth } from "@/context/AuthContext"; // if you’re storing token in context

export default function ApplyPage() {
  const { id } = useParams(); // job ID
  const router = useRouter();
  const [coverLetter, setCoverLetter] = useState("");
  const [resume, setResume] = useState(null);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState("");
  const { token, user } = useAuth(); // assuming you have auth context

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);

    const data = {
      job: id,
      applicant: user?.id,
      cover_letter: coverLetter,
      resume,
    };

    try {
      const response = await createApplication(data, token);
      if (response.id) {
        setMessage("✅ Application submitted successfully!");
        setTimeout(() => router.push("/jobs"), 2000);
      } else {
        setMessage("❌ Failed to apply. Please try again.");
      }
    } catch (error) {
      console.error("Error applying:", error);
      setMessage("⚠️ Something went wrong.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <section className="max-w-xl mx-auto py-12 px-6">
      <h1 className="text-3xl font-bold mb-6 text-gray-900">
        Apply for Job #{id}
      </h1>

      <form onSubmit={handleSubmit} className="space-y-5">
        <div>
          <label className="block font-medium mb-1 text-gray-700">
            Cover Letter
          </label>
          <textarea
            value={coverLetter}
            onChange={(e) => setCoverLetter(e.target.value)}
            required
            className="w-full border border-gray-300 rounded-lg p-3 focus:outline-none focus:ring-2 focus:ring-blue-500"
            rows="5"
          ></textarea>
        </div>

        <div>
          <label className="block font-medium mb-1 text-gray-700">
            Upload Resume
          </label>
          <input
            type="file"
            onChange={(e) => setResume(e.target.files[0])}
            required
            className="block w-full border border-gray-300 rounded-lg p-2"
          />
        </div>

        <button
          type="submit"
          disabled={loading}
          className="w-full bg-blue-600 text-white py-3 rounded-xl font-semibold hover:bg-blue-700 transition"
        >
          {loading ? "Submitting..." : "Submit Application"}
        </button>
      </form>

      {message && <p className="mt-4 text-center text-gray-700">{message}</p>}
    </section>
  );
}
